<template>
  <div class="box">
    <el-card class="card">
      <!--  -->
      <BreadLink :menuName="id ? '' : ''" />
      <el-form ref="formRef" :model="article" label-width="80px" :rules="rules">
        <el-form-item label="" prop="title">
          <el-input
            placeholder=""
            style="width: 400px"
            v-model="article.title"
          />
        </el-form-item>
        <el-form-item
          label=""
          prop="content"
          style="height: 400px; min-width: 750px"
          id="editor"
        >
          <QuillEditor
            :modules="modules"
            ref="editorRef"
            @ready="ready"
            toolbar="full"
            contentType="html"
            v-model:content="article.content"
          />
        </el-form-item>
        <el-form-item label="" id="cover">
          <el-radio-group v-model="article.cover.type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
          <div style="display: flex">
            <template v-if="article.cover.type > 0">
              <UploadCover
                style="margin-right: 15px"
                :key="cover"
                v-for="(cover, index) in article.cover.type"
                @getCoverImgs="getCoverImgs($event, index)"
                :imgSrc="article.cover.images[index]"
              />
            </template>
          </div>
        </el-form-item>
        <el-form-item label="" prop="channel_id">
          <el-select v-model="article.channel_id" placeholder="">
            <el-option
              :key="channel.id"
              v-for="channel in channels"
              :label="channel.name"
              :value="channel.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSubmit(false)">{{
            id ? "" : ""
          }}</el-button>
          <el-button @click="handleSubmit(true)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { QuillEditor } from "@vueup/vue-quill";
import "@vueup/vue-quill/dist/vue-quill.snow.css";
import imageUploader from "quill-image-uploader";
import { reactive, ref, toRef } from "vue";
import { publishArt, updateArt, getChannels, getArt } from "../../api/article";
import { useRoute, useRouter } from "vue-router";
import { ElMessage } from "element-plus";
import BreadLink from "../../components/BreadLink.vue";
import { uploadImage } from "../../api/images";
import UploadCover from "./components/UploadCover.vue";
export default {
  components: {
    QuillEditor,
    BreadLink,
    UploadCover,
  },
  setup() {
    //
    const modules = reactive({
      name: "imageUploader",
      module: imageUploader,
      options: {
        upload: (file) => {
          const fd = new FormData();
          fd.append("image", file);
          // return promise
          // return 
          return uploadImage(fd).then((res) => {
            return res.data.url;
          });
        },
      },
    });
    const route = useRoute();
    const router = useRouter();
    //
    let channels = ref([]);
    const formRef = ref(null);
    const editorRef = ref(null);
    //id
    let id = ref(route.query.id);
    //
    let type = null;
    //
    let article = ref({
      title: "",
      content: "",
      cover: { type: 0, images: [] },
      channel_id: null,
    });
    //
    const rules = reactive({
      title: [
        { required: true, message: "", trigger: "blur" },
        { min: 5, max: 30, message: " 5  30 ", trigger: "blur" },
      ],
      content: [
        { required: true, message: "", trigger: "blur" },
        //    p 
        {
          validator(rule, value, callback) {
            if (value === "<p></p>") {
              callback(new Error(""));
            } else {
              callback();
            }
          },
        },
      ],
      channel_id: [{ required: true, message: "" }],
    });
    //
    const getCoverImgs = (url, index) => {
      console.log(url, index);
      article.value.cover.images[index] = url;
    };
    //
    const getOneArticle = async () => {
      const res = await getArt(id.value);
      if (res.message === "OK") {
        article.value = res.data;
        type = res.data.cover.type;
        console.log(article);
      }
    };
    //
    const ready = () => {
      if (id.value) {
        getArt(id.value).then((res) => {
          console.log(res.data.content);
          editorRef.value.setHTML(res.data.content);
        });
      }
    };
    //
    if (id.value) {
      getOneArticle();
    }
    //
    const loaclChannels = async () => {
      const res = await getChannels();
      channels.value = res.data.channels;
    };
    loaclChannels();
    //
    const handleSubmit = (draft = false) => {
      console.log(draft);
      //
      formRef.value.validate(async (valid, err) => {
        if (!valid) return;
        //
        if (id.value) {
          let updataImages = article.value.cover.images;
          let updataType = article.value.cover.type;
          if (type !== updataType) {
            if (type == 3 && updataType == 0) {
              updataImages.splice(0);
            }
            if (type == 3 && updataType == 1) {
              updataImages.splice(1, 2);
            }
            if (type == 1 && updataType == 0) {
              updataImages.splice(0);
            }
          }
          //
          const res = await updateArt(id.value, article.value, draft);
          if (res.message === "OK") {
            ElMessage.success(`${draft ? "" : ""}~`);
            router.push("/article");
          }
        } else {
          //
          const res = await publishArt(article.value, draft);
          if (res.message == "OK") {
            ElMessage.success(`${draft ? "" : ""}~`);
            router.push("/article");
          }
        }
      });
    };

    return {
      modules,
      editorRef,
      formRef,
      article,
      rules,
      channels,
      id,
      handleSubmit,
      getCoverImgs,
      ready,
    };
  },
};
</script>

<style lang="scss" scoped>
@media screen and (min-width: 1536px) {
  #cover {
    margin-top: 70px;
  }
}
@media screen and (max-width: 1535px) {
  #cover {
    margin-top: 120px;
  }
}
.box {
  .card {
    #editor {
      // background: #000 !important;
      .ql-container.ql-snow {
        background: #000 !important;
      }
    }
  }
}
</style>