<template>
  <div class="box" @click="() => (dialogVisible = true)">
    <div class="cover-box">
      <img :src="imgSrc" class="cover-img" ref="imgRef" fit="cover" />
    </div>
    <el-dialog title="" v-model="dialogVisible" append-to-body>
      <!-- tab -->
      <el-tabs v-model="tabsSwitch" type="card">
        <el-tab-pane label="" name="first">
          <ImgList
            :isShowSelected="true"
            :isShowButton="false"
            ref="imgListRef"
          />
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <input ref="inputRef" type="file" @change="handleChooseFile" />
          <img :src="pUrl" alt="" />
        </el-tab-pane>
      </el-tabs>
      <template #footer>
        <el-button type="primary" @click="handleCoverConfirm"></el-button>
        <el-button @click="() => (dialogVisible = false)"> </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ref } from "vue";
import { uploadImage } from "../../../api/images";
import ImgList from "../../images/components/ImgList";
import { ElMessage } from "element-plus";
export default {
  props:['imgSrc'],
  components: { ImgList },
  setup(props, context) {
    //
    let dialogVisible = ref(false);
    //tabs
    let tabsSwitch = ref("first");
    let inputRef = ref(null);
    let imgListRef = ref(null);
    let imgRef=ref(null)
    //
    let pUrl = ref("");
    const handleChooseFile = () => {
      const file = inputRef.value.files[0];
      pUrl.value = window.URL.createObjectURL(file);
    };
    //
    const handleCoverConfirm = async () => {
      const file = inputRef.value.files[0];
      //
      if (tabsSwitch.value === "second") {
        if (!file) {
          ElMessage.info("");
          return;
        }
        const fd = new FormData();
        fd.append("image", file);
        const res = await uploadImage(fd);
        if (res.message === "OK") {
          // 
          imgRef.value.src = res.data.url;
          //  
          context.emit("getCoverImgs", res.data.url);
          // 
          dialogVisible.value = false;
        }
        //
      } else if (tabsSwitch.value === "first") {
        const selected = imgListRef.value.selected;
        if (selected === null) {
          ElMessage.info("");
          return;
        }
        imgRef.value.src = imgListRef.value.imgList[selected].url;
        context.emit("getCoverImgs", imgListRef.value.imgList[selected].url);
        dialogVisible.value = false;
      }
    };
    return {
      imgRef,
      pUrl,
      imgListRef,
      inputRef,
      tabsSwitch,
      dialogVisible,
      handleCoverConfirm,
      handleChooseFile,
    };
  },
};
</script>

<style lang="scss" scoped>
.box {
  width: 100px;
  height: 120px;
  border: 1px solid #ccc;
  .cover-img {
    height: 120px;
    max-width: 100%;
  }
}
</style>