import request  from "./request";

// 
export const getArticles = params => {
  return request({
    method: 'GET',
    url: '/articles',
    // GET
    // babydata
    // Queryparams
    // Headers  headers
    params
  })
}

// 
export const getChannels = () => {
  return request({
    method: 'GET',
    url: '/channels'
  })
}

// 
export const deleteArt = articleId => {
  return request({
    method: 'DELETE',
    url: `/articles/${articleId}`
  })
}
// 
export const publishArt = (data, draft = false) => {
  return request({
    method: 'POST',
    url: '/articles',
    params: { //  true
      draft
    },
    data
  })
}
// 
export const updateArt = (id, data, draft = false) => {
  return request({
    method: 'PUT',
    url: `/articles/${id}`,
    params: { //  true
      draft
    },
    data
  })
}

// 
export const getArt = (id) => {
  return request({
    method: 'GET',
    url: `/articles/${id}`
  })
}
// 
export const updataCommentStatus = (articleId, allowComment) => {
  return request({
    method: 'PUT',
    url: '/comments/status',
    params: {
      article_id: articleId
    },
    data: {
      allow_comment: allowComment
    }
  })
}